import {
	mapMutations,
	mapState
} from 'vuex'
export default {
	data() {
		return {
			defaultImg: '../../static/prod.png'
		}
	},
	methods: {
		lianxikefu() {
			uni.makePhoneCall({
				phoneNumber: this.machObject.servicePhone
			})
		},
		gobackmian() {
			uni.reLaunch({
				url: '../index/index'
			})
		},
		closeBle() {
			var that = this
			//关闭连接
			uni.closeBLEConnection({
				deviceId: that.deviceId,
				success(res) {
					uni.closeBluetoothAdapter({
						success(res) {
							console.log('页面隐藏后关闭蓝牙成功')
						}
					})
				}
			})
		}

	},
	onLoad() {
		this.closeBle()
		//页面再次显示的时候直接跳转到主页
		// uni.reLaunch({
		// 	url: '../index/index'
		// })
	},
	computed: {
		...mapState(['userId', 'goodsObjectArray', 'machObject', 'deviceId']),
		totalPrice() {
			let pric = 0
			let goa = this.goodsObjectArray
			for (var i = 0; i < goa.length; i++) {
				let singleProduc = goa[i]
				pric += singleProduc.product.goods.price * singleProduc.number
			}
			return pric
		}
	}
}
